/*------------------------------------*\
	General
\*------------------------------------*/

/* -- typography -- */

body {
  font-family: 'Varela Round','Times New Roman',sans-serif,Times;
  font-size: 16px;
  line-height: 1.6;
  overflow-x: hidden;
  color: #868F9B;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  margin-top: 0;
  margin-bottom: 20px;
  color: #10161A;
}

h1 {
  font-size: 70px;
}

h2 {
  font-size: 36px;
}

h3 {
  font-size: 21px;
}

h4 {
  font-size: 18px;
}

h5 {
  font-size: 16px;
}

a {
  color: #6195FF;
  text-decoration: none;
  -webkit-transition: 0.2s opacity;
  transition: 0.2s opacity;
}

a:hover, a:focus {
  text-decoration: none;
  outline: none;
  opacity: 0.8;
  color: #6195FF;
}

.main-color {
  color: #6195FF;
}

.white-text {
  color: #FFF;
}

#TitleText {
  /*font-size: 60px;
    font-weight: 300;
    letter-spacing: -0.5px;
    margin-bottom: 1.5rem;
    */
  margin-top: 10vh;
  font-family: Garamond, Baskerville, "Baskerville Old Face";
  color: black;
  color: rgba(0, 0, 0, 0.8);
  position: relative;
}


p.TitleTagline {
  color: #000;
  text-align: center;
  max-width: 1200px;
  font-size: 2rem;
}

::-moz-selection {
  background-color: #6195FF;
  color: #FFF;
}

::selection {
  background-color: #6195FF;
  color: #FFF;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none
}

/* -- section  -- */

.section {
  position: relative;
}

.md-padding {
  padding-top: 120px;
  padding-bottom: 120px;
}

.sm-padding {
  padding-top: 60px;
  padding-bottom: 60px;
}

/* --  background section  -- */

.bg-grey {
  background-color: #FAFAFA;
  border-top: 1px solid #EEE;
  border-bottom: 1px solid #EEE;
}

.bg-white {
  background-color: #FFF;
  border-top: 1px solid #EEE;
  border-bottom: 1px solid #EEE;
}

.bg-dark {
  background-color: #1C1D21;
}

/* --  background image section  -- */

.bg-img {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
}

.bg-img .overlay {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  opacity: .8;
  background: #1C1D21;
  /*#1C1D21;*/
}

/* --  section header  -- */

.section-header {
  position: relative;
  margin-bottom: 60px;
}

.section-header .title {
  text-transform: capitalize;
}

.title:after {
  content: "";
  display: block;
  height: 4px;
  width: 40px;
  background-color: #6195FF;
  margin-top: 20px;
}

.text-center .title:after {
  margin: 20px auto 0px;
}

/* --  subsection header  -- */

.subsection-header {
  position: relative;
  margin-bottom: 40px;
}

.subtitle:after {
  content: "";
  display: block;
  height: 2px;
  width: 40px;
  background-color: #FF5916;
  margin-top: 10px;
}

.text-center .subtitle:after {
  margin: 10px auto 0px;
}

/* --  Input  -- */

input[type="text"], input[type="email"], input[type="password"], input[type="number"], input[type="date"], input[type="url"], input[type="tel"], textarea {
  height: 40px;
  width: 100%;
  border: none;
  background: #F4F4F4;
  border-bottom: 2px solid #EEE;
  color: #354052;
  padding: 0px 10px;
  opacity: 0.5;
  -webkit-transition: 0.2s border-color, 0.2s opacity;
  transition: 0.2s border-color, 0.2s opacity;
}

textarea {
  padding: 10px 10px;
  min-height: 80px;
  resize: vertical;
}

input[type="text"]:focus, input[type="email"]:focus, input[type="password"]:focus, input[type="number"]:focus, input[type="date"]:focus, input[type="url"]:focus, input[type="tel"]:focus, textarea:focus {
  border-color: #6195FF;
  opacity: 1;
}

/* --  Buttons  -- */

.main-btn, .white-btn, .outline-btn {
  display: inline-block;
  padding: 10px 35px;
  margin: 3px;
  border: 2px solid transparent;
  border-radius: 3px;
  -webkit-transition: 0.2s opacity;
  transition: 0.2s opacity;
}

.main-btn {
  background: #6195FF;
  color: #FFF;
}

.white-btn {
  background: #FFF;
  color: #10161A !important;
}

.outline-btn {
  background: transparent;
  color: #6195FF !important;
  border-color: #6195FF;
}

.main-btn:hover, .white-btn:hover, .outline-btn:hover {
  opacity: 0.8;
}

/*------------------------------------*\
	Logo
\*------------------------------------*/

.navbar-brand {
  padding: 0;
}

.navbar-brand .logo, .navbar-brand .logo-alt {
  max-height: 50px;
  display: block;
}

#nav:not(.nav-transparent):not(.fixed-nav) .navbar-brand .logo-alt {
  display: none;
}

#nav.nav-transparent:not(.fixed-nav) .navbar-brand .logo {
  display: none;
}

#nav.fixed-nav .navbar-brand .logo-alt {
  display: none;
}


@media only screen and (max-width: 767px) {
  #nav.nav-transparent .navbar-brand .logo-alt {
    display: none !important;
  }
  #nav.nav-transparent .navbar-brand .logo {
    display: block !important;
  }
}


/*------------------------------------*\
	Navigation
\*------------------------------------*/

#nav {
  padding: 10px 0px;
  background: #AF5542;
  -webkit-transition: 0.2s padding;
  transition: 0.2s padding;
  z-index: 999;
}

#nav.navbar {
  border: none;
  border-radius: 0;
  margin-bottom: 0px;
}


#nav.fixed-nav {
  position: fixed;
  left: 0;
  right: 0;
  padding: 0px 0px;
  /*color of nav bar when scrolled down*/
  background-color: rgba(10, 10, 10, 0.8) !important;
  border-bottom: 1px solid #EEE;
}

#nav.nav-transparent {
  background: transparent;
}

/* -- default nav -- */

@media only screen and (min-width: 768px) {
  .main-nav li {
    padding: 0px 15px;
  }
  .main-nav li a {
    font-size: 14px;
    -webkit-transition: 0.2s color;
    transition: 0.2s color;
  }
  .main-nav>li>a {
    /*color of text in the nav bar when scrolled down*/
    color: #FFFFFF;
    padding: 15px 0px;
  }
  #nav.nav-transparent:not(.fixed-nav) .main-nav>li>a {
    color: #000000;
  }
  .main-nav>li>a:hover, .main-nav>li>a:focus, .main-nav>li.active>a {
    background: transparent;
    color: #6195FF;
  }
  .main-nav>li>a:after {
    content: "";
    display: block;
    background-color: #6195FF;
    height: 2px;
    width: 0%;
    -webkit-transition: 0.2s width;
    transition: 0.2s width;
  }
  .main-nav>li>a:hover:after, .main-nav>li.active>a:after {
    width: 100%;
  }
  /* dropdown */
  .has-dropdown {
    position: relative;
  }
  .has-dropdown>a:before {
    font-family: "FontAwesome";
    /*font-weight: 400;*/
    content: "\f054";
    font-size: 6px;
    margin-left: 6px;
    float: right;
    -webkit-transform: rotate(90deg);
    -ms-transform: rotate(90deg);
    transform: rotate(90deg);
    -webkit-transition: 0.2s transform;
    transition: 0.2s transform;
  }
  .dropdown {
    position: absolute;
    right: -50%;
    top: 0;
    background-color: #6195FF;
    width: 200px;
    -webkit-box-shadow: 0px 5px 5px -5px rgba(53, 64, 82, 0.2);
    box-shadow: 0px 5px 5px -5px rgba(53, 64, 82, 0.2);
    -webkit-transform: translateY(15px) translateX(50%);
    -ms-transform: translateY(15px) translateX(50%);
    transform: translateY(15px) translateX(50%);
    opacity: 0;
    visibility: hidden;
    -webkit-transition: 0.2s all;
    transition: 0.2s all;
  }
  .main-nav>.has-dropdown>.dropdown {
    top: 100%;
    right: 50%;
  }
  .main-nav>.has-dropdown>.dropdown .dropdown.dropdown-left {
    right: 150%;
  }
  .dropdown li a {
    display: block;
    color: #FFF;
    border-top: 1px solid rgba(250, 250, 250, 0.1);
    padding: 10px 0px;
  }
  .dropdown li:nth-child(1) a {
    border-top: none;
  }
  .has-dropdown:hover>.dropdown {
    opacity: 1;
    visibility: visible;
    -webkit-transform: translateY(0px) translateX(50%);
    -ms-transform: translateY(0px) translateX(50%);
    transform: translateY(0px) translateX(50%);
  }
  .has-dropdown:hover>a:before {
    -webkit-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  .nav-collapse {
    display: none;
  }
}

/* -- mobile nav -- */

@media only screen and (max-width: 767px) {
  #nav {
    padding: 0px 0px;
  }
  /*color of the nav bar when loaded */
  #nav.nav-transparent {
    background: rgba(0, 0, 0, 0.8);
  }
  .main-nav {
    position: fixed;
    right: 0;
    height: calc(100vh - 80px);
    -webkit-box-shadow: 0px 80px 0px 0px #1C1D21;
    box-shadow: 0px 80px 0px 0px #1C1D21;
    max-width: 250px;
    width: 0%;
    -webkit-transform: translateX(100%);
    -ms-transform: translateX(100%);
    transform: translateX(100%);
    margin: 0;
    overflow-y: auto;
    background: #1C1D21;
    -webkit-transition: 0.2s all;
    transition: 0.2s all;
  }
  #nav.open .main-nav {
    -webkit-transform: translateX(0%);
    -ms-transform: translateX(0%);
    transform: translateX(0%);
    width: 100%;
  }
  .main-nav li {
    border-top: 1px solid rgba(250, 250, 250, 0.1);
  }
  .main-nav li a {
    display: block;
    color: #FFF;
    -webkit-transition: 0.2s all;
    transition: 0.2s all;
  }
  .main-nav>li.active {
    border-left: 6px solid #6195FF;
  }
  .main-nav li a:hover, .main-nav li a:focus {
    background-color: #6195FF;
    color: #FFF;
    opacity: 1;
  }
  .has-dropdown>a:after {
    font-family: "FontAwesome";
    /*font-weight: 400;*/
    content: "\f054" ;
    float: right;
    -webkit-transition: 0.2s -webkit-transform;
    transition: 0.2s -webkit-transform;
    transition: 0.2s transform;
    transition: 0.2s transform, 0.2s -webkit-transform;
  }
  .dropdown {
    opacity: 0;
    visibility: hidden;
    height: 0;
    background: rgba(250, 250, 250, 0.1);
  }
  .dropdown li a {
    padding: 6px 10px;
  }
  .has-dropdown.open-drop>a:after {
    -webkit-transform: rotate(90deg);
    -ms-transform: rotate(90deg);
    transform: rotate(90deg);
  }
  .has-dropdown.open-drop>.dropdown {
    opacity: 1;
    visibility: visible;
    height: auto;
    -webkit-transition: 0.2s all;
    transition: 0.2s all;
  }
}

/* -- nav btn collapse -- */

.nav-collapse {
  position: relative;
  float: right;
  width: 40px;
  height: 40px;
  margin-top: 5px;
  margin-right: 5px;
  cursor: pointer;
  z-index: 99999;
}

.nav-collapse span {
  display: block;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  position: absolute;
  top: 50%;
  left: 50%;
  width: 25px;
}

.nav-collapse span:before, .nav-collapse span:after {
  content: "";
  display: block;
}

.nav-collapse span, .nav-collapse span:before, .nav-collapse span:after {
  height: 4px;
  background: #10161A;
  -webkit-transition: 0.2s all;
  transition: 0.2s all;
}

.nav-collapse span:before {
  -webkit-transform: translate(0%, 10px);
  -ms-transform: translate(0%, 10px);
  transform: translate(0%, 10px);
}

.nav-collapse span:after {
  -webkit-transform: translate(0%, -14px);
  -ms-transform: translate(0%, -14px);
  transform: translate(0%, -14px);
}

#nav.open .nav-collapse span {
  background: transparent;
}

#nav.open .nav-collapse span:before {
  -webkit-transform: translateY(0px) rotate(-135deg);
  -ms-transform: translateY(0px) rotate(-135deg);
  transform: translateY(0px) rotate(-135deg);
}

#nav.open .nav-collapse span:after {
  -webkit-transform: translateY(-4px) rotate(135deg);
  -ms-transform: translateY(-4px) rotate(135deg);
  transform: translateY(-4px) rotate(135deg);
}

