/*------------------------------------*\
	Pricing
\*------------------------------------*/

.pricing {
  position: relative;
  text-align: center;
  border: 1px solid #EEE;
  background-color: #FFF;
  z-index: 11;
  margin: 15px 0px;
}

.pricing::after {
  content: "";
  background-color: #1C1D21;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  height: 0%;
  z-index: -1;
  -webkit-transition: 0.2s height;
  transition: 0.2s height;
}

.pricing:hover:after {
  height: 100%;
}

.pricing .price-head {
  position: relative;
  margin-bottom: 20px;
}

.pricing .price-title {
  display: block;
  padding: 40px 0px 20px;
  text-transform: uppercase;
  -webkit-transition: 0.2s color;
  transition: 0.2s color;
}

.pricing:hover .price-title {
  color: #6195FF;
}

.pricing .price {
  position: relative;
  width: 140px;
  height: 140px;
  line-height: 140px;
  text-align: center;
  margin: auto;
  border-radius: 50%;
  border: 2px solid #6195FF;
}

.pricing .price h3 {
  font-size: 42px;
  margin: 0px;
  -webkit-transition: 0.2s color;
  transition: 0.2s color;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  top: 50%;
  position: absolute;
  left: 0;
  right: 0;
}

.pricing:hover .price h3 {
  color: #fff;
}

.pricing .duration {
  display: block;
  font-size: 14px;
  text-transform: uppercase;
  color: #10161A;
  -webkit-transition: 0.2s color;
  transition: 0.2s color;
}

.pricing:hover .duration {
  color: #fff;
}

.pricing .price-btn {
  padding-top: 20px;
  padding-bottom: 40px;
}

